.wrapper {
  display: none;
  position: relative;
  width: 35px;
  height: 20px;
  justify-self: center;
  align-self: center;
}
.item {
  position: absolute;
  width: 100%;
  height: 12%;
  border-radius: 50px;
  left: 50%;
  transform: translateX(-50%);
  background: #c4a370;
}
.item_1 {
  top: 0px
}
.item_2 {
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.item_3 {
  bottom: 0px;
}
@media (max-width: 800px) {
  .wrapper {
    display: block;
  }
}