.wrapper {
  position: relative;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
.mainTopBlock {
  position: relative;
  display: grid;
  width: 100%;
  grid-template-columns: 50% 50%;
}
.back {
  position: relative;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 800;
  margin: 30px 0;
  display: flex;
  padding-left: 50px;
  grid-gap: 5px;
}
.back img {
  justify-self: center;
  align-self: center;
  height: 10px;
}
.back p {
  text-transform: capitalize;
  font-weight: 600;
  font-size: 110%;
}
@media (max-width: 800px) {
  .mainTopBlock {
    grid-template-columns: 1fr;
  }
  .wrapper {
    position: relative;
    margin: 0 auto;
  }
  .back {
    margin: 20px 0;
    padding-left: 5%;
  }
}
