.checkbox-auth-item-none {
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.checkbox-auth-block {
  display: block;
  position: relative;
  margin: 0px 0px;
  margin-bottom: 7px;
  width: max-content;
  left: 5px;
}
.checkbox-auth-span {
  position: relative;
  margin-left: 0px;
  display: flex;
  width: max-content;
  font-size: 70%;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
  opacity: 0.7;
}
.checkbox-auth-item-block {
  transition: 0.2s;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border: 1px solid var(--color);
  margin-right: 10px;
}
.checkbox-auth-name{
  white-space: nowrap;
  font-weight: 700;
  transition: 0.2s;
  color: var(--color);
}
.checkbox-auth-item-1-1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 50%;
  border-radius: 2px;
  background: var(--color);
  overflow: hidden;
  opacity: 0;
  transition: 0.2s;
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-item-block {
  border: 1px solid var(--color);
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-name {
}
.checkbox-auth-block:hover .checkbox-auth-span .checkbox-auth-item-block {
  border: 1px solid var(--color);
}
.checkbox-auth-block:hover .checkbox-auth-span .checkbox-auth-name {
  color: var(--color);
}
.checkbox-auth-item-none:checked + .checkbox-auth-span .checkbox-auth-item-block .checkbox-auth-item-1-1 {
  overflow: visible;
  opacity: 1;
}
@media (max-width: 800px) {
  .checkbox-auth-item-block {
    width: 30px;
    height: 30px;
  }
  .checkbox-auth-span {
    font-size: 90%;
  }
}