.wrapper {
  position: relative;
  width: 100%;
}
.block {
  position: relative;
  width: 100%;
  justify-self: center;
  border: none;
}
.iconBack {
  position: relative;
  width: 100%;
}
.wrapperBlock {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
}
.wrapperItemsBlock {
  position: relative;
  width: 200px;
  height: 100px;
  margin-top: 10px;
}
.wrapperBlockSlide {
}
.wrapperItemsBlockSlide {
  position: relative;
  cursor: pointer;
  transition: 0.3s;
}
/*.wrapperItemsBlockSlide .swiper-slide-thumb-active{*/
/*  background: red;*/
/*}*/
.wrapperBlockSlide img{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.wrapperItemsBlockSlide img{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}