.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  width: 75%;
  height: 100vh;
  background: var(--backgroundColor);
  transition: 0.3s ease;
  transform: translateX(0%);
  z-index: 101;
  padding: 15px 30px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}
.fon {
  position: fixed;
  top: 0px;
  right: 0px;
  background: rgba(0, 0, 0, 0.36);
  width: 100vw;
  height: 100vh;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;
}
.fonOn {
  opacity: 1;
  visibility: visible;
}
.wrapperOff {
  transform: translateX(100%);
}
.btnBack {
  position: relative;
  outline: none;
  border: none;
  background: none;
  height: 30px;
  align-self: start;
  margin-left: -20px;
}
.btnBack img{
  position: relative;
  height: 100%;
}
.settingBlock {
  position: absolute;
  bottom: 30px;
  width: 100%;padding: 20px;
  background: red;
  display: flex;
  display: none;
  box-sizing: border-box;
}
.settingBlock a {
  font-weight: 800;
  font-size: 130%;
  text-transform: uppercase;
}
.menuBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  margin-top: 30px;
}
.menuBlock p {
  position: relative;
  font-weight: 700;
  font-size: 130%;
  text-transform: uppercase;
}
.menuBlockShopItem {
  position: relative;
  display: flex;
  grid-gap: 10px;
  align-items: center;
  transition: 0.1s;
}
.menuBlockShopItem:active {
  opacity: 0.5;
}
.menuBlockShopItemOffIcon {
  position: relative;
  height: 10px;
  width: 5px;
  transform: translateY(1px) rotate(0deg);
  transition: 0.3s;
}
.menuBlockShopItemOnIcon {
  transform: translateY(1px) rotate(90deg);
}
.name {
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 140%;
}
.orders {
  transition: 0.3s;
  position: relative;
  margin-top: 30px;
  display: flex;
  grid-gap: 10px;
}
.orders:active {
  opacity: 0.5;
}
.orders p{
  font-size: 130%;
  font-weight: 700;
  text-transform: uppercase;
}
.orders img{
  align-self: center;
  transform: translateY(1px);
  height: 20px;
}