.wrapper {
  position: relative;
  width: 100%;
  background: #423b34;
  padding: 15px;
  text-align: center;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.wrapper p {
}
.content {
  font-weight: 600;
  font-size: 120%;
  color: white;
}
.to {
  font-weight: 600;
  width: max-content;
  margin: 0 auto;
  font-size: 100%;
  transition: 0.3s;
}
.to:hover {
  opacity: 0.7;
}
.btnClose {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  height: 20px;
  cursor: pointer;
}
.btnClose img{
  position: relative;
  height: 100%;
}
@media (max-width: 800px) {
  .wrapper {
    padding: 15px 45px;
    padding-left: 15px;
  }
  .content {
    font-size: 100%;
  }
  .btnClose {
    height: 15px;
    right: 15px;
  }
  .to {
    font-weight: 600;
    width: max-content;
    margin: 0 auto;
    font-size: 90%;
    transition: 0.3s;
  }
}