.wrapper {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.block {
  display: flex;
  flex-direction: column;
  padding: 30px 15px 20px;
  border: 1px solid #c4a370;
  width: 90%;
  max-width: 350px;
  border-radius: 5px;
  transform: translateY(-50px);
}
.logo {
  transform: translateY(-50px);
  width: 80px;
  margin-bottom: 30px;
}
.name {
  text-align: center;
  font-weight: 600;
  font-size: 120%;
}
.block button {
  background: #c4a370;
  color: var(--backgroundColor);
  width: 100%;
  padding: 15px;
  font-weight: 600;
  border-radius: 5px;
}
.backPage {
  position: absolute;
  top: 30px;
  left: 30px;
  display: flex;
  justify-content: center;
  grid-gap: 10px;
  font-weight: 600;
  font-size: 100%;
}
.backPage img{
  height: 20px;
  width: 30px;
}
.bottomBlock {
  display: flex;
  width: 100%;
  margin-top: 20px;
}
@media (max-width: 800px) {
  .backPage {
    top: 30px;
    left: 5%;
  }
  .block button {
    padding: 20px;
    font-size: 110%;
  }
}