.switch {
  width: 80px;
  height: 40px;
  background: none;
  border: 2px solid var(--color);
  display: flex;
  justify-content: flex-start;
  border-radius: 10px;
  padding: 5px;
  cursor: pointer;
}

.switch[data-isOn="true"] {
  justify-content: flex-end;
}

.handle {
  width: 28px;
  height: 28px;
  align-self: center;
  background-color: var(--color);
  border-radius: 7px;
}
