.wrapper {
  position: relative;
  padding: 0;
  margin: 20px 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 100;
}
.block {
  position: relative;
  display: flex;
  grid-gap: 30px;
}
.block a {
}
@media (max-width: 800px) {
  .wrapper {
    display: none;
  }
}

