.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  margin-left: 40px;
}
.name {
  position: relative;
  font-weight: 500;
  font-size: 150%;
  text-transform: uppercase;
}
.price {
  position: relative;
  font-weight: 500;
  font-size: 150%;
}
.price span{
  position: relative;
  font-size: 80%;
  font-weight: 400;
}
.description {
  white-space: break-spaces;
  margin: 0px;
}
.btnAddFavorites {
  height: 20px;
  cursor: pointer;
  align-self: start;
  display: flex;
  grid-gap: 10px;
  margin: 10px 0;
  justify-items: center;
  align-items: center;
}
.btnAddFavorites p{
  font-size: 100%;
  font-weight: 600;
  text-transform: uppercase;
}
.btnAddFavorites img {
  position: relative;
  height: 100%;
}
@media (max-width: 800px) {
  .wrapper {
    margin-left: 0px;
    margin-top: 30px;
  }
}