.wrapper {
  position: relative;
  padding-top: 0;
  padding-bottom: 30px;
  width: 90%;
  margin: 0 auto;
}
.block {
  position: relative;
}
.name {
  text-align: center;
  font-weight: 200;
  opacity: 0.8;
}
.navBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.navBlock a {
  font-weight: 400;
}
.requisitesBlock {
  position: relative;
  text-align: center;
}
.long {
  position: relative;
  width: 100%;
  height: 1px;
  background: #c4a370;
  border-radius: 50%;
  margin: 30px 0;
  opacity: 0.5;
}
.BankIconBlock {
  position: relative;
  display: flex;
  opacity: 0.4;
  justify-content: center;
  grid-gap: 10px;
  margin: 20px 0;
}
.BankIconBlock img{
  height: 20px;
}
@media (max-width: 800px) {
  .BankIconBlock {
    display: grid;
    grid-template-columns: 1fr ;
    width: min-content;
    margin: 20px auto;
    justify-items: center;
  }
  .long {
    width: 100%;
  }
}