.wrapper {
  position: relative;
  width: 100%;
  height: min-content;
  margin-left: 10px;
}
.name {
  position: relative;
  font-weight: 600;
  font-size: 130%;
  display: flex;
  align-items: center;
  grid-gap: 6px;
  text-transform: capitalize;
  transition: 0.3s;
}
.name:active {
  opacity: 0.5;
}
.nameIcon {
  position: relative;
  width: 5px;
  height: 10px;
  transform: translateY(3px);
  transition: 0.3s;
}
.nameIconOn {
  transform: translateY(3px) rotate(90deg);
}
.childBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 0;
  transition: 0.3s;
  width: auto;
  margin-left: 5px;
  padding-left: 10px;
  margin-top: 10px;
  grid-gap: 5px;
  overflow: hidden;
}
.childBlock:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 2px;
  opacity: 0.3;
  height: 100%;
  border-radius: 50%;
  background: #c4a370;
}
.childBlock a {
  position: relative;
  font-weight: 500;
  font-size: 110%;
  white-space: normal;
  text-transform: capitalize;
}
.childBlockOn {
  height: auto;
}