.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 15px 0;
}
.block_left {
  display: flex;
  align-items: center;
  grid-gap: 30px;
  width: 100%;
  min-width: 100px;
}
.block_left p{
  font-weight: 500;
}
.photo {
  position: relative;
  overflow: hidden;
  min-width: 150px;
  min-height: 150px;
}
.photo img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.block_center {
  align-self: center;
  width: min-content;
  justify-self: end;
  margin-right: 10%;
}
.block_right {
  width: min-content;
  align-self: center;
  justify-self: end;
}