.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  margin-top: 10px;
}
.name {
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 120%;
}
.menuBlockShopItem {
  position: relative;
  display: flex;
  grid-gap: 10px;
  align-items: center;
}
.menuBlockShopItem img {
  position: relative;
  height: 10px;
  width: 8px;
  transform: translateY(1px) rotate(90deg);
}