.blockMenu {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: calc(100% - 100px);
}
.searchLeft {
  display: block;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  height: 25px;
  width: max-content;
}
.menuRightBlock {
  position: relative;
  display: flex;
  grid-gap: 20px;
  margin-left: auto;
  margin-right: 0px;
  height: max-content;
}
.menuRightBlock a img {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  height: 100%
}
.menuRightBlock a {
  height: 25px;
}
.blockMenu button img{
   position: relative;
   height: 100%;
}
.menuRightBlock button {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  height: 25px;
  padding: 0;
}
.blockMenu a img{
}
.basket_icon {}
.profile_icon img {
  position: relative;
}
.searchRight {
  display: none;
  position: relative;
}
.logoHeaderHide {}
.love_icon {}
.blockMenu a, button {
  transition: 0.2s;
}
.logo {
  position: relative;
  width: 100px;
  height: 100px;
  justify-self: center;
  align-self: center;
  transition: width 0.1s, height 0.1s;
}
.logo img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
}
.blockMenu a:active, button:active {
  opacity: 0.5;
}
.blockMenu a:hover, button:hover {
  opacity: 0.5;
}
@media (max-width: 800px) {
  .blockMenu {
    grid-template-columns: 1fr 1fr;
    width: 90%;
  }
  .searchLeft {
    display: none;
  }
  .logo {
    position: relative;
    width: 40px;
    height: 40px;
    justify-self: start;
    align-self: center;
  }
  .menuRightBlock {
    position: relative;
    display: flex;
    grid-gap: 15px;
    align-self: center;
  }
  .searchRight {
    display: block;
  }
}