
.btnBlock {
  position: relative;
  display: flex;
  height: 60px;
  grid-gap: 20px;
}
.btnAddBasket {
  white-space: nowrap;
  padding: 0px 20px;
  width: 100%;
  max-width: 250px;
  background: #c4a370;
  color: var(--backgroundColor);
  font-size: 90%;
  font-weight: 700;
  cursor: pointer;
  border: 2px solid #c4a370;
  text-transform: uppercase;
}
.btnAddBasket:hover {
  background: none;
  color: #c4a370;
  opacity: 1;
}
.quantityBlock {
  position: relative;
  border: 1px solid #c4a370;
  display: flex;
  width: max-content;
}
.quantityBlock span {
  position: relative;
  height: 100%;
  width: 30px;
  border: none;
  outline: none;
  cursor: pointer;
  background: none;
  font-weight: 200;
  font-size: 300%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quantityBlock span img {
  position: absolute;
  left: 50%;
  width: 30%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.quantityInput {
  text-align: center;
  position: relative;
  width: 40px;
  height: 100%;
  border: none;
  outline: none;
  padding: 0;
  font-size: 130%;
  font-weight: 600;
  background: none;
}