.formBlock {
  display: flex;
  flex-direction: column;
  width: 100%;
  grid-gap: 10px;
  margin: 20px 0 0;
}
.formBlock input {
  border: 1px solid #c4a370;
  padding: 15px;
  font-size: 100%;
  border-radius: 5px;
  background: none;
  font-weight: 400;
}
@media (max-width: 800px) {
  .formBlock input {
    padding: 20px;
    font-size: 120%;
  }
}