.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 30px 0;
}
.block {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px 30px;
}
@media (max-width: 800px) {
  .block {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 500px) {
  .block {
    grid-template-columns: 1fr;
    grid-gap: 20px 30px;
  }
}
