.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
}
.wrapper a, .wrapper p {
  position: relative;
  font-weight: 700;
  font-size: 130%;
  text-transform: uppercase;
  transition: 0.3s;
}
.wrapper a:active {
  opacity: 0.5;
}
.menuBlockShopItem {
  position: relative;
  display: flex;
  grid-gap: 10px;
  align-items: center;
}
.menuBlockShopItem img {
  position: relative;
  height: 10px;
  width: 8px;
  transform: translateY(1px);
}